import React from "react";
import '../logn.css';
class login extends React.Component{
    click=()=>{
        document.querySelector('.lo-f').style.height='100%';
    }
    render(){
        return(
            <div className="l-f">
            <div className="lo-f">
            <div> <fieldset>
                    <legend>Ti khon</legend>
                    <input onClick={()=>this.click()} className="pass"></input>
                </fieldset></div></div>
              <div className="lo-f"> <div><fieldset>
                    <legend>Mt khu</legend>
                    <input onClick={()=>this.click()} className="id"></input>
                </fieldset></div></div>
                <div className="lo-d">  <button>NG NHP</button></div>
              
            </div>
          
        )
    }
}
export default login;